@layer components {
  .ch-button {
    @apply text-xl inline-block px-6 py-3 no-underline transition-all duration-300;
    &.primary {
      @apply shadow-[inset-0_0_0_2px_transparent] text-white rounded-[10px] bg-darkest-blue hover:bg-hover-blue;
    }
    &.secondary {
      @apply rounded-[10px] bg-transparent shadow-[inset_0_0_0_1px_#d4d5dc] hover:shadow-[inset_0_0_0_2px_#1d225f];
    }
    &.tertiary {
      @apply rounded-[10px] bg-bright-teal hover:bg-lightest-teal hover:text-dark-blue;
    }
  }
  .nav-parent-menu {
    &:hover {
      .nav-sub-menu {
        @apply md:opacity-100 md:visible;
        img {
          @apply opacity-100 translate-x-0;
        }
        h6 {
          @apply opacity-100 translate-x-0;
        }
      }
      svg {
        @apply rotate-180;
      }
    }
    .nav-sub-menu {
      @apply md:opacity-0 invisible rounded-b-xl h-0 md:h-auto shadow-xl min-w-full;
      & > .menu-container {
        @apply md:block hidden;
      }
      &.active {
        & > .menu-container {
          @apply md:block block;
        }
        @apply h-auto visible;
      }
      .nav-link {
        &.sub-link {
          @apply opacity-0 translate-y-1 border-0;
        }
      }
      img {
        @apply opacity-0 -translate-x-1 transition-all duration-150 delay-150;
      }
      h6 {
        @apply opacity-0 -translate-x-1 transition-all duration-300 delay-150;
      }
    }
    svg {
      @apply rotate-0 transition-transform duration-300;
    }
  }
  .nav-link {
    @apply text-white no-underline md:py-5 md:px-8 px-0 py-4  md:hover:bg-lightest-teal md:hover:text-dark-blue transition-colors duration-300 text-lg block;
    &.sub-link {
      @apply md:py-3 md:px-5 py-3 px-4 ml-4 md:ml-0 whitespace-nowrap;
    }
  }
  .slide-out {
    @apply right-full md:right-0 md:w-auto transition-right duration-500 w-full px-4 md:px-0;
    &.active {
      @apply right-0;
    }
  }
  /* .site-main {
  @apply transition-transform duration-500;
  &.active {
    @apply translate-x-1/2;
  }
} */
}
