@layer utilities {
  .wp-block-group {
    @apply py-sp-11;
  }
  .alignleft {
    /*rtl:ignore*/
    float: left;

    /*rtl:ignore*/
    margin-right: 1.5em;
    margin-bottom: 1.5em;
  }

  .alignright {
    /*rtl:ignore*/
    float: right;

    /*rtl:ignore*/
    margin-left: 1.5em;
    margin-bottom: 1.5em;
  }

  .aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1.5em;
  }

  /* Captions
--------------------------------------------- */
  .wp-caption {
    margin-bottom: 1.5em;
    max-width: 100%;
  }

  .wp-caption img[class*='wp-image-'] {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .wp-caption .wp-caption-text {
    margin: 0.8075em 0;
  }

  .wp-caption-text {
    text-align: center;
  }

  /* Galleries
--------------------------------------------- */
  .gallery {
    margin-bottom: 1.5em;
    display: grid;
    grid-gap: 1.5em;
  }

  .gallery-item {
    display: inline-block;
    text-align: center;
    width: 100%;
  }

  .gallery-columns-2 {
    grid-template-columns: repeat(2, 1fr);
  }

  .gallery-columns-3 {
    grid-template-columns: repeat(3, 1fr);
  }

  .gallery-columns-4 {
    grid-template-columns: repeat(4, 1fr);
  }

  .gallery-columns-5 {
    grid-template-columns: repeat(5, 1fr);
  }

  .gallery-columns-6 {
    grid-template-columns: repeat(6, 1fr);
  }

  .gallery-columns-7 {
    grid-template-columns: repeat(7, 1fr);
  }

  .gallery-columns-8 {
    grid-template-columns: repeat(8, 1fr);
  }

  .gallery-columns-9 {
    grid-template-columns: repeat(9, 1fr);
  }

  .gallery-caption {
    display: block;
  }
  .resize-animation-stopper * {
    transition: none !important;
    animation: none !important;
  }

  .container {
    @apply max-w-[1280px] mx-auto px-5;
  }
  .container-small {
    @apply max-w-[840px] mx-auto;
  }
  /* EDITOR */
  /* .mce-content-body {
